import Vue from 'vue'
import VueRouter from 'vue-router'
import * as echarts from 'echarts'
Vue.prototype.$echarts = echarts
import by from '../views/3-1.vue'

Vue.use(VueRouter)

const routes = [

  {
    path: '/',
    name: 'by',
    component: by
  }

]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
