<template>
<section class="bg-alt hero p-0">
	<div class="container-fluid">
		<div class="row">
			<div class="col-md-4 bg-white  text-dark py-5 text-center border border-secondary" id="right">
				<h1 class=" h2">基于服务迁移的边缘网络适配优化 </h1>
        <hr/>
                
                <div class="pt-2 mb-5"></div>
                <p class="mt-4 lead ">
                  请输入网络中产生的服务请求的数据密集型（dl）和计算密集型（cl）任务的比例：
                </p>
                <div class="input-group mb-3">
                    <input id='input'  v-model="input" type="text" class="form-control" placeholder="0.2" aria-label="Recipient's username" aria-describedby="button-addon2">
                    <div class="input-group-append">
                      <button class="btn btn-primary " type="button" id='ok' @click="onOk" >确定</button>
                    </div>
                </div>

                <div class="pt-2 mt-5 mb-5 d-flex flexItem">
                    <button  class="btn btn-primary btn-lg"  @click="onGreedy" :disabled="this.GreedyDisabled ==-1">
                      贪婪算法
                    </button>
                    <button  class="btn btn-success btn-lg" @click="onNsga2" :disabled="this.RLDisabled ==-1">
                      遗传算法
                    </button>
                </div>

                <div class="mb-3">
                  <img src="../static/imgs/3.png" alt="Male" class="img-thumbnail img-fluid rounded-circle"/>
                </div>  
                <div class="pt-2 mb-5"></div>             
                <table class="table">
                    <thead class="thead-light">
                      <tr>
                        <th scope="col">Task</th>
                        <th scope="col">Description</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        <th scope="row">tsk1.dpt</th>
                        <td>Video stream gathering.</td>
                      </tr>
                      <tr>
                        <th scope="row">tsk2.dpt</th>
                        <td>Video stream interception.</td>
                      </tr>
                      <tr>
                        <th scope="row">tsk3.dpt</th>
                        <td>Image processing.</td>
                      </tr>
                      <tr>
                        <th scope="row">tsk4.dpt</th>
                        <td>Acoustic analysis.</td>
                      </tr>
                      <tr>
                        <th scope="row">tsk5.dpt</th>
                        <td>Speed analysis.</td>
                      </tr>
                    </tbody>
                </table>

			</div>
			<div class="col-md-7 ml-auto px-0 py-2">


				<!--算法性能对比-->
				<section class="bg-alt border border-secondary mb-5 " style="width: 900px; " >
          <h1 class=" h4 m-3">算法性能对比 </h1>
              <div class="container-fluid  mb-5   border-secondary" >
                <div class="row d-md-flex  " >
                   <div class="chart ml-5">   
                      <div id="awardsdata" :style="{width:'400px',height:'300px'}"></div>
                   </div>        
                   <div class="chart1 ">   
                      <div id="awardsdata1" :style="{width:'400px',height:'300px'}"></div>
                   </div>
                </div>
              </div>

				</section>

        <!-- 间隔 -->
        <div style="height:20px;"></div>                

				<!--算法演示过程-->
          <section class="bg bg-white mt-5   border border-secondary" id="row" style="position=relative">	             
            <h1 class=" h4 m-3">算法演示过程 </h1>
          </section>	
        
			</div>
		</div>
	</div>
</section>
</template>


<script src="http://ajax.aspnetcdn.com/ajax/jQuery/jquery-1.8.0.js"></script>
<script>
import $ from 'jquery';
import axios from 'axios';
let echarts = require("echarts/lib/echarts");
require("echarts/lib/chart/bar");
require("echarts/lib/component/tooltip");
require("echarts/lib/component/legend");
export default {
  data() {
    return {
      input: '',
      deviceSetting:null,
      serviceFlag:null,
      deviceInit:null,
      oldX:[],
      oldY:[],
        greedyErgCon:null,
        greedyDelayCon:null,
        nsga2ErgCon:null,
        nsga2DelayCon:null,
        chart1Option:{
        tooltip: {
          trigger: "axis",
        },
        grid: {
          left: '20%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: [
          {
            type: "category",
            data: [ "贪婪算法","遗传算法"],
            axisLine: {
              symbol : ['none', 'arrow'],
            },    
            axisTick: {
              show: false
            },
            axisLabel:{
              fontSize:22,
              color:"rgba(42, 42, 42, 1)",
            }
          },
        ],
        yAxis: [
          {
            type: "value" ,
            name:"能量消耗/J",
            nameTextStyle: {
            fontSize: 22,color:"rgba(42, 42, 42, 1)",
            },
            splitLine:{show: false}, 
            axisLine: {
              show:true,
              symbol : ['none', 'arrow'],
            },
            axisLabel:{         
              fontSize:22,
              color:"rgba(42, 42, 42, 1)",
            }
          }
       ],
        series: [
          {
            type: "bar",
            data: [this.greedyErgCon,this.nsga2ErgCon],
            barWidth: 25,
            label: {
              fontSize: 200
            }
          },
        ],
      },
      chart2Option:{
        tooltip: {
          trigger: "axis",
        },
        grid: 
        {
        left: '20%',
        right: '4%',
        bottom: '3%',
        containLabel: true
        },
        legend: {
          x: "500px",
          y: "480px",
          data: [ "贪婪算法","遗传算法"],
        },
        xAxis: [
          {
            type: "category",
            data:["贪婪算法","遗传算法"],
            axisLine: {
              symbol : ['none', 'arrow']
            },   
            axisTick: {
              show: false
            },
            axisLabel:{
              fontSize:22,
              color:"rgba(42, 42, 42, 1)",
            }
          },
        ],
        yAxis: [
          {
            type: "value" ,
            name:"时间延迟/s",
            nameTextStyle: {
              fontSize: 22,color:"rgba(42, 42, 42, 1)",
            },
            splitLine:{show: false}, 
            axisLine: {
              show:true,
              symbol : ['none', 'arrow'],
            },
            axisLabel:{
              fontSize:22,
              color:"rgba(42, 42, 42, 1)",
            },
          }
       ],
        series: [
          {
            type: "bar",
            data: null,
            barWidth: 25,
            label: {
              fontSize: 200
            }
          },
        ],
      }
    }
  },
  methods:{
    chart() {
      let chart1 = echarts.init(document.getElementById("awardsdata"));
    },


    option() {
      let chart2 = echarts.init(document.getElementById("awardsdata1"));
    },

    createDeviceSetting(deviceSetting){
      $("#row").children().filter(".dc").each(function(){
          $(this).remove();
        });
      for(var obj in deviceSetting){
        let a = document.getElementById("row");
        let div = document.createElement("div");
        div.style.width='60px';
        div.style.height='80px';
        deviceSetting[obj]["x"] = deviceSetting[obj]["x"] * 3 /5
        deviceSetting[obj]["y"] = deviceSetting[obj]["y"] * 3 /5
        div.style.left = deviceSetting[obj]["x"]*3+'px';
        div.style.top = 145+deviceSetting[obj]["y"]*1.2+'px';
        div.style.position="absolute";
        let img = document.createElement("img");
        img.style.width='60px';
        img.style.height='80px';
        img.setAttribute("src", require("@/static/imgs/dc.png"));
        div.append(img);
        div.className="dc";
        //div.id=deviceSetting[obj]["id"];
        // var spanObj = document.createElement('span');
        // spanObj.innerHTML=parseInt(deviceSetting[obj]["name"].split("th")[0])+1;
        // spanObj.style="text-align:center;display:block;"
        // div.appendChild(spanObj);
        a.appendChild(div);
      }
    },
    createCommunicationRange(communicationRange){
      $("#row").children().filter(".dc1").each(function(){
          $(this).remove();
        });
        var pointX=[];
        var pointY=[];
        for(var obj in communicationRange){
          var point=communicationRange[obj]
          for(var obj2 in this.deviceSetting){
            if(obj2==point){
              pointX.push(this.deviceSetting[obj2]["x"]*3);
              pointY.push(145+this.deviceSetting[obj2]["y"]*1.2);
            }
          }
        }
      for(let i=0;i<pointX.length;i++){
        let a = document.getElementById("row");
        let div = document.createElement("div");
        div.style.width='60px';
        div.style.height='80px';
        div.style.left = pointX[i]+'px'
        div.style.top = pointY[i]+'px';
        let img = document.createElement("img");
        img.style.width='60px';
        img.style.height='80px';
        img.setAttribute("src", require("@/static/imgs/dc1.png"));
        div.append(img);
        div.style.position="absolute";
        div.className="dc1"
        a.appendChild(div);  
      } 
    },
    random(serviceFlag,deviceInit){
      $("#row").children().filter(".device").each(function(){
          $(this).remove();
        });
      var pointX=[];
      var pointY=[];
      for(var obj in deviceInit){
        var point=deviceInit[obj]
        for(var obj2 in this.deviceSetting){
          if(obj2==point){
            pointX.push(this.deviceSetting[obj2]["x"]*3);
            pointY.push(140+this.deviceSetting[obj2]["y"]*1.2);
          }
        }
      }
      for(let i=0;i<pointX.length;i++){
        let a = document.getElementById("row");
        let div = document.createElement("div");
        div.style.width='15px';
        div.style.height='15px';
        div.style.left = pointX[i]+7+'px'
        div.style.top = pointY[i]+5+'px';
        let img = document.createElement("img");
        img.style.width='15px';
        img.style.height='15px';
        if(serviceFlag[i]==0){
          img.setAttribute("src", require("@/static/imgs/dl.png"));
        }
        else if(serviceFlag[i]==1){
          img.setAttribute("src", require("@/static/imgs/cl.png"));
        }
        div.append(img);
        div.style.position="absolute";
        div.id="div"+i;
        div.className="device"
        a.appendChild(div);  
      }
      var oldX=[]
      var oldY=[]
      $("#row").children().filter(".device").each(function(){
        oldX.push(parseInt(this.style.left.slice(0,this.style.left.length-2)))
        oldY.push(parseInt(this.style.top.slice(0,this.style.top.length-2)))
      }); 
      this.oldX=oldX;
      this.oldY=oldY;
    },
    getRepeatNum(deviceId,index){ 
      var arr=deviceId.slice(0,parseInt(index)+1);
      var obj = {}; 
      for(var i= 0; i< arr.length; i++){ 
          var item = arr[i]; 
          obj[item] = (obj[item] +1 ) || 1; 
      } 
      return obj; 
    },
    onGreedy(){
      this.random(this.serviceFlag,this.deviceInit);
      //初始坐标
      var oldX=this.oldX
      var oldY=this.oldY
      var newX=[]
      var newY=[]
      axios
        .get('/greedy')
        .then(response => {
          var pointX=[];
          var pointY=[];
          var deviceId=response.data.data.deviceId;
          console.log(deviceId)
          this.createCommunicationRange(deviceId)
          for(var obj in deviceId){
            var point=deviceId[obj]

            for(var obj2 in this.deviceSetting){
              if(obj2==point){
                var repeatNum=parseInt(this.getRepeatNum(deviceId,obj)[JSON.parse(point)])-1
                pointX.push(this.deviceSetting[obj2]["x"]*3+8+repeatNum*15);
                pointY.push(145+this.deviceSetting[obj2]["y"]*1.2);
              }
            }
          }
          for(let i=0;i<pointX.length;i++){
            newX.push(parseInt(pointX[i]))
            newY.push(parseInt(pointY[i]))
          }
          //移动
          let div0 = document.getElementById("div0");
          let div1 = document.getElementById("div1");
          let div2 = document.getElementById("div2");
          let div3 = document.getElementById("div3");
          let div4 = document.getElementById("div4");
          var timer1= setInterval(function () {
            if(div0.offsetLeft==newX[0])
            {
                clearInterval(timer1);
            }
            else if(oldX[0]<newX[0]){
              div0.style.left=div0.offsetLeft+1+'px'
            }
            else{
              div0.style.left=div0.offsetLeft-1+'px'
            }
          },10);  
          var timer2= setInterval(function () {
            if(div0.offsetTop==newY[0])
            {
                clearInterval(timer2);
            }
            else if(oldY[0]<newY[0]){
              div0.style.top=div0.offsetTop+1+'px'
            }
            else{
              div0.style.top=div0.offsetTop-1+'px'
            }
          },10);  
          //
          var timer3= setInterval(function () {
            if(div1.offsetLeft==newX[1])
            {
                clearInterval(timer3);
            }
            else if(oldX[1]<newX[1]){
              div1.style.left=div1.offsetLeft+1+'px'
            }
            else{
              div1.style.left=div1.offsetLeft-1+'px'
            }
          },10);  
          var timer4= setInterval(function () {
            if(div1.offsetTop==newY[1])
            {
                clearInterval(timer4);
            }
            else if(oldY[1]<newY[1]){
              div1.style.top=div1.offsetTop+1+'px'
            }
            else{
              div1.style.top=div1.offsetTop-1+'px'
            }
          },10);  
          //
          var timer5= setInterval(function () {
            if(div2.offsetLeft==newX[2])
            {
                clearInterval(timer5);
            }
            else if(oldX[2]<newX[2]){
              div2.style.left=div2.offsetLeft+1+'px'
            }
            else{
              div2.style.left=div2.offsetLeft-1+'px'
            }
          },10);  
          var timer6= setInterval(function () {
            if(div2.offsetTop==newY[2])
            {
                clearInterval(timer6);
            }
            else if(oldY[2]<newY[2]){
              div2.style.top=div2.offsetTop+1+'px'
            }
            else{
              div2.style.top=div2.offsetTop-1+'px'
            }
          },10);  
          //
          var timer7= setInterval(function () {
            if(div3.offsetLeft==newX[3])
            {
                clearInterval(timer7);
            }
            else if(oldX[3]<newX[3]){
              div3.style.left=div3.offsetLeft+1+'px'
            }
            else{
              div3.style.left=div3.offsetLeft-1+'px'
            }
          },10);  
          var timer8= setInterval(function () {
            if(div3.offsetTop==newY[3])
            {
                clearInterval(timer8);
            }
            else if(oldY[3]<newY[3]){
              div3.style.top=div3.offsetTop+1+'px'
            }
            else{
              div3.style.top=div3.offsetTop-1+'px'
            }
          },10);  
          //
          var timer9= setInterval(function () {
            if(div4.offsetLeft==newX[4])
            {
                clearInterval(timer9);
            }
            else if(oldX[4]<newX[4]){
              div4.style.left=div4.offsetLeft+1+'px'
            }
            else{
              div4.style.left=div4.offsetLeft-1+'px'
            }
          },10);  
          var timer10= setInterval(function () {
            if(div4.offsetTop==newY[4])
            {
                clearInterval(timer10);
            }
            else if(oldY[4]<newY[4]){
              div4.style.top=div4.offsetTop+1+'px'
            }
            else{
              div4.style.top=div4.offsetTop-1+'px'
            }
          },10);  
            this.greedyErgCon = response.data.data.conErg;
            this.greedyDelayCon = response.data.data.conTime;
            
            let chart1 = echarts.init(document.getElementById("awardsdata"));
            this.chart1Option.series[0].data= [this.greedyErgCon,this.nsga2ErgCon];
            chart1.setOption(this.chart1Option);
            let chart2 = echarts.init(document.getElementById("awardsdata1"));
            this.chart2Option.series[0].data= [this.greedyDelayCon,this.nsga2DelayCon];
            chart2.setOption(this.chart2Option);
      })
    },
    onNsga2(){
      this.random(this.serviceFlag,this.deviceInit);
      //初始坐标
      var oldX=this.oldX
      var oldY=this.oldY
      var newX=[]
      var newY=[]
      axios
        .get('/nsga2')
        .then(response => {
          var pointX=[];
          var pointY=[];
          var deviceId=response.data.data.deviceId;
          this.createCommunicationRange(deviceId)
          console.log(deviceId)
          for(var obj in deviceId){
            var point=deviceId[obj]
            for(var obj2 in this.deviceSetting){
              if(obj2==point){
                var repeatNum=parseInt(this.getRepeatNum(deviceId,obj)[JSON.parse(point)])-1
                pointX.push(this.deviceSetting[obj2]["x"]*3+8+repeatNum*15);
                pointY.push(145+this.deviceSetting[obj2]["y"]*1.2);
              }
            }
          }
          for(let i=0;i<pointX.length;i++){
            newX.push(parseInt(pointX[i]))
            newY.push(parseInt(pointY[i]))
          }
          //移动
          let div0 = document.getElementById("div0");
          let div1 = document.getElementById("div1");
          let div2 = document.getElementById("div2");
          let div3 = document.getElementById("div3");
          let div4 = document.getElementById("div4");
          var timer1= setInterval(function () {
            if(div0.offsetLeft==newX[0])
            {
                clearInterval(timer1);
            }
            else if(oldX[0]<newX[0]){
              div0.style.left=div0.offsetLeft+1+'px'
            }
            else{
              div0.style.left=div0.offsetLeft-1+'px'
            }
          },10);  
          var timer2= setInterval(function () {
            if(div0.offsetTop==newY[0])
            {
                clearInterval(timer2);
            }
            else if(oldY[0]<newY[0]){
              div0.style.top=div0.offsetTop+1+'px'
            }
            else{
              div0.style.top=div0.offsetTop-1+'px'
            }
          },10);  
          //
          var timer3= setInterval(function () {
            if(div1.offsetLeft==newX[1])
            {
                clearInterval(timer3);
            }
            else if(oldX[1]<newX[1]){
              div1.style.left=div1.offsetLeft+1+'px'
            }
            else{
              div1.style.left=div1.offsetLeft-1+'px'
            }
          },10);  
          var timer4= setInterval(function () {
            if(div1.offsetTop==newY[1])
            {
                clearInterval(timer4);
            }
            else if(oldY[1]<newY[1]){
              div1.style.top=div1.offsetTop+1+'px'
            }
            else{
              div1.style.top=div1.offsetTop-1+'px'
            }
          },10);  
          //
          var timer5= setInterval(function () {
            if(div2.offsetLeft==newX[2])
            {
                clearInterval(timer5);
            }
            else if(oldX[2]<newX[2]){
              div2.style.left=div2.offsetLeft+1+'px'
            }
            else{
              div2.style.left=div2.offsetLeft-1+'px'
            }
          },10);  
          var timer6= setInterval(function () {
            if(div2.offsetTop==newY[2])
            {
                clearInterval(timer6);
            }
            else if(oldY[2]<newY[2]){
              div2.style.top=div2.offsetTop+1+'px'
            }
            else{
              div2.style.top=div2.offsetTop-1+'px'
            }
          },10);  
          //
          var timer7= setInterval(function () {
            if(div3.offsetLeft==newX[3])
            {
                clearInterval(timer7);
            }
            else if(oldX[3]<newX[3]){
              div3.style.left=div3.offsetLeft+1+'px'
            }
            else{
              div3.style.left=div3.offsetLeft-1+'px'
            }
          },10);  
          var timer8= setInterval(function () {
            if(div3.offsetTop==newY[3])
            {
                clearInterval(timer8);
            }
            else if(oldY[3]<newY[3]){
              div3.style.top=div3.offsetTop+1+'px'
            }
            else{
              div3.style.top=div3.offsetTop-1+'px'
            }
          },10);  
          //
          var timer9= setInterval(function () {
            if(div4.offsetLeft==newX[4])
            {
                clearInterval(timer9);
            }
            else if(oldX[4]<newX[4]){
              div4.style.left=div4.offsetLeft+1+'px'
            }
            else{
              div4.style.left=div4.offsetLeft-1+'px'
            }
          },10);  
          var timer10= setInterval(function () {
            if(div4.offsetTop==newY[4])
            {
                clearInterval(timer10);
            }
            else if(oldY[4]<newY[4]){
              div4.style.top=div4.offsetTop+1+'px'
            }
            else{
              div4.style.top=div4.offsetTop-1+'px'
            }
          },10);  
            this.nsga2ErgCon = response.data.data.conErg;
            this.nsga2DelayCon = response.data.data.conTime;
            
            let chart1 = echarts.init(document.getElementById("awardsdata"));
            this.chart1Option.series[0].data= [this.greedyErgCon,this.nsga2ErgCon];
            chart1.setOption(this.chart1Option);
            let chart2 = echarts.init(document.getElementById("awardsdata1"));
            this.chart2Option.series[0].data= [this.greedyDelayCon,this.nsga2DelayCon];
            chart2.setOption(this.chart2Option);
      })
    },
    onOk() {
      $("#row").children().filter(".dc1").each(function(){
        $(this).remove();
      });
      const ratio=document.getElementById('input').value
      const payload={"ratio":ratio}
      axios.post('/getRatio',payload)
      .then(response => {
        var deviceInit=response.data.data.deviceInit;
        this.deviceSetting=response.data.data.deviceSetting;
        this.createDeviceSetting(response.data.data.deviceSetting)
        // this.createCommunicationRange(response.data.data.communicationRange)
        this.serviceFlag=response.data.data.serviceFlag;
        this.deviceInit=response.data.data.deviceInit;
        this.random(response.data.data.serviceFlag,response.data.data.deviceInit)
      });
    },
  },
      mounted(){
      let chart1 = echarts.init(document.getElementById("awardsdata"));
      let chart2 = echarts.init(document.getElementById("awardsdata1"));
      chart1.setOption(this.chart1Option);
      chart2.setOption(this.chart2Option);
    }
}
</script>




<style>
  .flexItem{
    Justify-content:Space-evenly;
  }
          .layui-side{

            top:0;
            bottom:0;

        }
        .rizhi {
          position: relative;
        }
        .tujie {
          position: absolute;
          bottom: 0px;
          right: 0px;
        }


  p{display: inline;}
    .bg {
      background:url("../static/imgs/background1.png") no-repeat center;
      background-size:contain;
      width: 900px;
      height: 600px;
      position: relative;
  }


 


</style>

